<template>
	<div id="deliveryer-location">
		<public-header title="配送员位置"></public-header>
		<div class="content">
			<deliveryer-location :deliveryer="deliveryer" v-if="deliveryer.length > 0"></deliveryer-location>
			<div class="icon icon-refresh" @click="onLoad(true)"></div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import deliveryerLocation from "@/components/deliveryerLocation"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
			deliveryerLocation
		},
		data() {
			return {
				deliveryer: [],
				ids: [],
				showPreLoading: true
			}
		},
		methods: {
			onLoad(force) {
				var that = this;
				if(force) {
					that.deliveryer = [];
				}
				getData({
					vue: that,
					url: 'plateform/deliveryer/plateform/location',
					data: {
						ids: that.ids,
					},
					success: function(res) {
						that.deliveryer = [...that.deliveryer, ...res.deliveryer];
					}
				})
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id) {
				this.ids.push(this.$route.query.id)
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#deliveryer-location .icon-refresh{
		position: absolute;
		top: 10px;
		right: 10px;
		width: 40px;
		height: 40px;
		line-height: 40px;
		border-radius: 50%;
		background-color: rgba(0,0,0,0.3);
		color: #fff;
		text-align: center;
		font-size: 18px;
	}
</style>


